* {
  box-sizing: border-box;
  font-family: 'Noto Sans', sans-serif;
}
.day {
  text-align: left;
  background-color: #fff;
  border: .5px solid #777;
  height: 128px; }

.day__button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  padding: 8px;
  overflow: auto;
  cursor: pointer; }

.day__reminders {
  list-style-type: none;
  font-size: 11px;
  padding: 8px 0;
  margin: 0; }

.day__reminder-item {
  padding: 2px 0; }

.day--empty {
  background-color: #ddd; }

.month {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  padding: 0 8px; }

.month__month-name {
  grid-column: 2 / 7; }

.month__button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  height: 100%;
  width: 100%;
  background-color: #fff;
  cursor: pointer; }

month__button-icon {
  size: 3em; }

.month__day-name {
  background-color: #999;
  border: .5px solid #777;
  font-size: 13px;
  padding: 4px; }

.reminder-editor {
  height: 100%;
  display: flex;
  flex-direction: column; }

.reminder-editor__close {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  background-color: #fff;
  padding: 0 8px 8px 0;
  font-size: 32px;
  color: #777;
  cursor: pointer; }
  .reminder-editor__close:hover {
    color: #333; }

.reminder-editor__heading {
  font-family: 'Noto Sans', sans-serif;
  font-size: 25px;
  color: #333;
  margin: 0;
  padding: 0 0 16px 0; }

.reminder-editor__input {
  width: 100%;
  padding: 8px;
  margin: 4px 0; }

.reminder-editor__button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  padding: 10px;
  margin: 4px 0;
  background-color: #f9f9f9;
  border: 1px solid #999;
  border-radius: 3px;
  width: 100%; }
  .reminder-editor__button:hover {
    background-color: #eee; }
  .reminder-editor__button:active {
    background-color: #ddd; }

.reminder-editor__color-button {
  background-color: #fff; }

.layout {
  font-family: 'Noto Sans', sans-serif;
  text-align: center; }

.h1,
.h2 {
  font-family: 'Noto Sans', sans-serif; }

